@use './_colors.scss';
@use './space.scss';
@use './_fonts.scss';

.quote {
  max-width: 700px;
  margin-inline: auto;
  text-align: center;
  font-size: 1.5rem;
  line-height: 1.5;

  &__author {
    font-weight: 700;
  }

  &__loading,
  &__error {
    text-align: center;
    font-size: 2.5rem;
    font-weight: 700;
  }
}
